import {documentToHtmlString, Options} from "@contentful/rich-text-html-renderer"
import {BLOCKS} from "@contentful/rich-text-types"
import {classes} from "~/utilities/classes"
import {ContentfulReference, RichContent} from "../../../types/RichContent"
import "./Reader.css"

const YOUTUBE_VIDEO_1 = "https://www.youtube.com/watch?v="
const YOUTUBE_VIDEO_2 = "https://youtu.be/"
const VIMEO_VIDEO = "https://vimeo.com/"
const CONTENTFUL_VIDEO = "https://videos.ctfassets.net/"
const CONTENTFUL_IMAGE = "https://images.ctfassets.net/"

export interface ReaderProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "children"> {
    children: RichContent
}

const Reader: React.FC<ReaderProps> = props => {
    const {children, className, ...otherPros} = props

    const referencesMap = new Map<string, ContentfulReference>()
    let options: Options | undefined = undefined

    if (children.references) {
        for (var reference of children.references) {
            referencesMap.set(reference.contentful_id, reference)
        }

        options = {
            renderNode: {
                [BLOCKS.EMBEDDED_ENTRY]: node => {
                    const id = (node.data as ReferenceData).target.sys.id
                    const reference = referencesMap.get(id)

                    let src = reference ? reference.url : ""

                    if (reference && reference.url.indexOf(YOUTUBE_VIDEO_1) > -1) {
                        src = `https://www.youtube.com/embed/${reference.url.replace(YOUTUBE_VIDEO_1, "")}`
                    }
                    if (reference && reference?.url.indexOf(YOUTUBE_VIDEO_2) > -1) {
                        src = `https://www.youtube.com/embed/${reference.url.replace(YOUTUBE_VIDEO_2, "")}`
                    }
                    if (reference && reference?.url.indexOf(VIMEO_VIDEO) > -1) {
                        src = `https://player.vimeo.com/video/${reference.url.replace(VIMEO_VIDEO, "")}`
                    }

                    if (reference?.height != "") {
                        return (
                            `<div class="ref-container-z2vt" style="height:` +
                            reference?.height +
                            `">
                        <iframe class="ref-z2vt"
                            src=${src}
                            frameBorder="0"
                            allowFullScreen
                        ></iframe>
                    </div>`
                        )
                    }

                    return `<div class="ref-container-z2vt" >
                        <iframe class="ref-z2vt"
                            src=${src}
                            frameBorder="0"
                            allowFullScreen
                        ></iframe>
                    </div>`
                },
                [BLOCKS.EMBEDDED_ASSET]: node => {
                    const id = (node.data as ReferenceData).target.sys.id
                    const reference = referencesMap.get(id)

                    if (reference && reference?.url.indexOf(CONTENTFUL_VIDEO) > -1) {
                        return `<div class="ref-container-z2vt">
                        <video class="ref-z2vt"
                        controls
                            playsInline
                            src=${reference.url}
                        ></video>
                    </div>`
                    }
                    if (reference && reference?.url.indexOf(CONTENTFUL_IMAGE) > -1) {
                        return `
                        <img class="ref-img-z2vt"
                            src=${reference.url}
                        ></img>
                    `
                    }

                    return ``
                },
            },
        }
    }

    return (
        <div
            className={classes("reader-z2vt", className)}
            {...(children
                ? {
                      dangerouslySetInnerHTML: {
                          __html: documentToHtmlString(JSON.parse(children.raw), options),
                      },
                  }
                : {})}
            {...otherPros}
        />
    )
}

export default Reader

type ReferenceData = {
    target: {sys: {id: string; type: string; linkType: string}}
}
